@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin-left: 2%;
  margin-right: 2%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Most PCs */
@media screen and (min-width: 1030px) {
  .container {  
    display: flexbox;
    position: relative;   
    flex-wrap: nowrap;
    flex-direction:row;  
    box-sizing: border-box;
  }
  
  .container img {  
    width: 20%;
    margin-top: -3%;
  }
  
  .container h1 {  
    margin-top: 1%;
    position: absolute;
    top: 10%;
    font-size: 2.5em;
    width: 250px;
    text-align: left;
    word-break: break-word;
  }
  
  .container SocialIcon {
    bottom: 10%;
  }

  .container p {
    position: absolute;
    top: 60%;
    width: 250px;
    text-align: left;
    word-break: break-word;
  }  
}

/* Large tablets */
@media screen and (min-width: 480px) and (max-width: 1030px) {
  .container {  
    display: flexbox;
    position: relative;   
    flex-wrap: nowrap;
    flex-direction:row;  
    box-sizing: border-box;
  }
  
  .container img {  
    width: 20%;
    margin-top: -3%;
  }
  
  .container h1 {  
    margin-top: 1%;
    position: absolute;
    top: 10%;
    font-size: 2.5em;
    width: 250px;
    text-align: left;
    word-break: break-word;
  }
  
  .container p {
    position: absolute;
    top: 80%;
    width: 250px;
    text-align: left;
    word-break: break-word;
  }  
}
  
/* Most Phones */
@media screen and (max-width: 480px) {
  .container {  
    display: flexbox;
    position: relative;   
    flex-wrap: nowrap;
    flex-direction:row;  
    box-sizing: border-box;
  }
  
  .container img {  
    width: 25%;
    margin-top: -3%;
  }
  
  .container h1 {  
    margin-top: 1%;
    position: absolute;
    top: 10%;
    font-size: 2em;
    width: 250px;
    text-align: left;
    word-break: break-word;
  }
  
  .container p {
    position: absolute;
    top: 50%;
    width: 250px;
    text-align: left;
    word-break: break-word;
  }  
}


